<template>
	<view class="box">
		<view class="member-box">
			<image lazy-load class="img"  :src="user.avatarUrl" mode="aspectFill"></image>
			<view class="name-box">
				<view>{{user.nickname?user.nickname:"未设置用户名"}}</view>
				<!-- <view class="phone">（159****2225 登录）</view> -->
			</view>
		</view>
		<view class="pay-box">
			<view class="title-box">
				<view class="left">
					会员专享
				</view>
				<view class="right">
					会员可提现 发布悬赏 获得奖励 分享有礼等操作>
				</view>
			</view>
			<template v-for="(item,k) in payList">
				<view class="pay-li" :class="{'active':chooseType==k}" :key="k" @tap="chooseTypes(k)">
					<view class="left">
						<view class="title">
							{{item.description}}
						</view>
						<view class="time">
							{{item.remarks}}
						</view>
					</view>
					<view class="right">
						￥{{item.value}}
					</view>
				</view>
			</template>
			<view class="title-box">
				<view class="left">
					选择支付方式
				</view>
			</view>
			<view class="pay-list">
				<view class="iconfont iconweixinzhifu "></view>
				<view class="iconfont iconlujing iconxuanzhong"></view>
			</view>
		</view>
		<view class="blank">
		</view>
		<view class="fix-box">
			<view class="left">
				总计：<text class="money font-bold" v-if="payList.length>0">￥{{payList[chooseType].value}}</text>
			</view>
			<view class="bg-btn" @tap="getPay">
				立即支付
			</view>
		</view>
	</view>
</template>

<script>
	import {
		mapState
	} from 'vuex';
	let loading = false
	export default {
		data() {
			return {
				rechargeMoney: "",
				memberType: "",
				payList: [],
				chooseType: 0,
				user: {
					avatarUrl: "",
					nickname: "",
					userId: "",
					memberId: "",
					loginType: "MOBILE",
				},
			}
		},
		computed: {
			...mapState({
				user_state: state => state.user,
			})
		
		},
		onLoad() {
			this.loadPayList();
			let user_json = this.user_state;
			this.user = {
				avatarUrl: user_json.avatarUrl || "",
				nickname: user_json.nickname || "",
				userId: user_json.userId || "",
				memberId: user_json.memberId || "",
				loginType: user_json.loginType || "MOBILE",
			}
		},
		methods: {
			chooseTypes(k) {
				this.chooseType = k
			},
			loadPayList() {
				this.$api.get('/sysDict/list', {
					params: {
						type: 'MEMBERTYPE'
					}
				}).then(res => {
					if (res.code && res.code != 200) {
						return
					};
					this.payList = res.list;
				})
			},
			getPay() {
				if (loading) return;
				loading = true;
				let rechargeMoney = this.payList[this.chooseType].value;
				let memberType = this.payList[this.chooseType].label;
				this.$api.post('/payInfo/becomeMemberPay', {
					userSeqId: this.$common.getLocalSync('customerSeqId'),
					memberType: memberType,
					payType: 1,
					rechargeMoney: rechargeMoney
				}).then(res => {
					loading = false
					if (res.code == 200) {
						this.weixinPay(res.data)
					} else if (res.code == 10012) {
						uni.navigateTo({
							url: '/pages/login/login_wechat'
						})
					} else {
						this.$box.confirm(res.msg, false)
					}
				})
			},
			weixinPay(data) {
				// #ifdef MP 
				let paymentData = data;
				uni.requestPayment({
					timeStamp: paymentData.timeStamp,
					nonceStr: paymentData.nonceStr,
					package: paymentData.package,
					signType: 'MD5',
					paySign: paymentData.paySign,
					success: (res) => {
						uni.showToast({
							title: "支付成功"
						})
					},
					fail: (res) => {
						uni.showModal({
							content: "支付失败",
							showCancel: false
						})
					},
					complete: (res) => {
						console.log(res);
					}
				})
				// #endif
			},
		}
	}
</script>

<style lang="scss">
	.fix-box {
		justify-content: space-between;
		align-items: center;

		.left {
			font-size: 24rpx;

			.money {
				margin-left: 10rpx;
				font-size: 26rpx;
				color: $color-primary;
			}
		}
	}

	.member-box {
		width: calc(100% -68rpx);
		padding: 24rpx 34rpx;
		background: white;
		display: flex;
		align-items: center;
		font-size: 34rpx;

		.img {
			width: 120rpx;
			height: 120rpx;
			border-radius: 100%;
			margin-right: 24rpx;
		}

		.phone {
			font-size: 26rpx;
			color: #666666;
			margin-left: -14rpx;
		}
	}

	.pay-box {
		width: calc(100% -40rpx);
		padding: 24rpx 20rpx;
		background: white;
		margin-top: 10rpx;

		.title-box {
			width: calc(100% -28rpx);
			padding: 0 14rpx;
			display: flex;
			justify-content: space-between;
			align-items: center;

			.left {
				font-size: 30rpx;
			}

			.right {
				font-size: 20rpx;
				color: #666666;
			}
		}

		.pay-li {
			width: 682rpx;
			height: 128rpx;
			background: rgba(247, 247, 247, 1);
			border: 1px solid rgba(190, 190, 190, 1);
			border-radius: 10rpx;
			display: flex;
			padding: 0 14rpx;
			margin: 40rpx 0;
			display: flex;
			justify-content: space-between;
			align-content: center;
			align-items: center;
			font-size: 30rpx;

			.time {
				color: #666;
				font-size: 26rpx;
				margin-top: 16rpx;
			}
		}

		.active {
			background: rgba(255, 235, 234, 1);
			border: 1px solid rgba(255, 158, 151, 1);
			color: #DF5950;

			.time {
				color: #EB817A;
			}
		}

		.pay-list {
			width: calc(100% - 28rpx);
			padding: 0 14rpx;
			display: flex;
			justify-content: space-between;
			align-items: center;
			font-size: 36rpx;
			margin: 16rpx 0;

			.iconweixinzhifu {
				font-size: 50rpx;
				color: rgb(9, 187, 7);
			}

			.iconxuanzhong {
				color: $color-primary;
			}
		}
	}
</style>
